.emphasis,.block {
  display: flex;
  flex-direction: row;
  align-items:center;
  justify-content: space-between;
  padding: 10px;
  margin: 20px;
  margin-bottom: 0px;

  min-height: 150px;
  display: flex;
  justify-content: center;
  text-align: justify;
}

.blockCard {
  flex: 3;
  text-align: center;
  padding: 10px;
  margin: 5px;
}

.blockText {

  flex: 7;
  text-align: justify;
  margin: 5px;
}


.emphasis {
  box-shadow: 2px 2px 2px 2px #c0c0c0;

}