.list{
    min-width:400px ;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    margin:100px;
    > li {
        word-break: break-all; /*Quebra o texto para evitar overflow*/
        height: auto;
        margin-top: 10px;
        border: solid;
        padding: 10px;
        border-radius: 10px;
        background-color: #E0E0E0;
    }
}


