.cardsCarousel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px;
  background-color: #ebebeb;
  box-shadow: 2px 2px 2px 2px #c0c0c0;
  flex-direction: column;
  max-width: auto;
  overflow: hidden; /* Oculta o conteúdo fora do contêiner */
  max-width: 100%; /* Defina o tamanho máximo do contêiner, ajuste conforme necessário */


}

.titleSetCards {
  padding: 10px;
  margin: 5px;
  color: black;
}
.setCard {
  display: flex;
  flex-direction: row;
  text-align: center;
  padding: 10px;
  margin: 5px;
  align-content: center;
  justify-content: start; /* Distribui os itens uniformemente na linha */
  transition: transform 0.75s; /* Adicione uma transição suave ao alterar a posição do carrossel */

}

.blockCards {
  align-items: stretch;
  text-align: center;
  flex: 0 0 100%; /* Tamanho fixo dos itens do carrossel (100% da largura do contêiner) */
  min-width: calc(50% - 10px); /* Define a largura mínima para caber 2 itens (50% / 2) com uma pequena margem entre eles */
  max-width: calc(25% - 10px); /* Define a largura máxima para caber 4 itens (100% / 4) com uma pequena margem entre eles */
}

.manipulation{
  width: 100%;
  display:flex;
  flex:0;
  justify-content: space-between;
}

.prev-button,
.next-button {
  background-color: #333;
  color: #fff;
  padding: 5px 10px;
  margin: 10px;
  border: none;
  cursor: pointer;
}

.prev-button {
  margin-right: auto;
}

.next-button {
  margin-left: auto;
}
